import React, {Component} from "react";
import PropTypes from "prop-types";
import Portal from "./Portal"
import {pickProps} from "eshop/modules/core/utils/ui";
import {loadModule, stopModules} from "eshop/modules/core/utils/ui";
var noTriggerTooltipId = 0;
class OraTooltip extends Component {
    constructor(props) {
        super(props);

    }
    componentWillMount() {
        var id = this.props.id?this.props.id:noTriggerTooltipId++;
        this.tooltipId = "react-tooltip-" + id;
        this.triggerId = "react-tooltip-" + id + "-trigger";
        this.contentClass = "react-tooltip-" + id + "-content";
    }

    componentDidMount() {
        this.loadModule();
    }
    loadModule(){
        var maxWidth=this.props.maxWidth?this.props.maxWidth:200;
        //Workaround dla NOR-111573. Tymczasowa poprawka, odkomentowane zostanie po poprawkach zespolu UI
        /*OPL.UI.loadModules(document.getElementById("tooltip-container-"+this.tooltipId),[
                {
                    path:"core/modules/tooltips",
                    options:
                    {
                        triggerSelector: '.o-tooltip__trigger',
                        contentSelector: '.o-tooltip__content',
                        maxWidth:maxWidth,
                        triggerEvent: 'mouseover'
                    }
                }


            ]);*/

        }
    componentWillUnmount() {
        OPL.UI.stopModules( this.ref )
    }

    render() {
        return (
        <div id={"tooltip-container_"+this.tooltipId} className={this.props.className}>
            <div id={"tooltip-container-"+this.tooltipId} ref={ref=> this.ref = ref} className="u-inline">
                    {this.props.children}
                        <Portal portalId={this.tooltipId} portalClassName={"u-hide--soft o-tooltip__content "+this.contentClass} role="tooltip"  getDestinationNode={()=> document.getElementsByClassName(this.contentClass)[0]}>

                            {this.props.tooltipContent}

                        </Portal>


            </div>
            </div>

         )
    }
}


OraTooltip.propTypes = {
    open: PropTypes.bool

};

OraTooltip.defaultProps = {

};

export default OraTooltip;
